var object = {
    //获取obj的长度
    getObjLen: function (obj) {
        var index = 0;
        this.each(obj, function () {
            ++index;
        });
        return index;
    },
    //获取obj的keys
    getObjKeys: function (object) {
        return this.each(object, function (obj, key) {
            return key;
        }, true);
    },
};
export default object;
