var regexp = {
    //获取表达式
    getRegExp: function (expr) {
        var tm = '\\/*.?+$^[](){}|\'\"';
        this.each(tm, function (tmItem, index) {
            expr = expr.replace(new RegExp('\\' + tmItem, 'g'), '\\' + tmItem);
        });
        return expr;
    },
};
export default regexp;
